import request, { apiResError } from './request';

// https://wiki.n.miui.com/pages/viewpage.action?pageId=290845598
// 注销前调用获取个人信息进行提示
export function logoffHint() {
    return request.get('/api/oauth/logouthint')
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}

// 注销
export function doLogoff() {
    return request.get('/api/oauth/logout')
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data);
        });
}
