<template>
    <div class="logoff">
        <NavBar title="账号注销" />
        <div class="logoff-text-box">
            <div class="logoff-title">注意事项</div>
            <div
                class="logoff-tips"
                v-for="(item, index) in tips"
                :key="index"
                v-html="item"/>
            <label
                class="checkbox">
                <input
                    v-model="isRead"
                    name="isread"
                    value="1"
                    class="logoff-check"
                    type="checkbox">
                <i
                    class="check-icon"
                    :class="{'checked-icon': isRead}"/>
                <span>
                    我已阅读并同意
                </span>
            </label>
            <Button
                class="logoff-button"
                :class="{'disabled': !isRead }"
                @click="logoff">继续注销</Button>
            <logoffConfirm
                :msg="hint"
                :show="showConfirm"
                @action="logoffAction"
                @cancel="cancel" />
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import { getConfigByKey } from '@/api/common';
import Button from '@/components/Button';
import { logoffHint, doLogoff } from '@/api/logoff';
import { toast, destroyAccount } from '@/utils/jsApiV2/client';
import { goIndexTab } from '@/utils/jsApiV2/deeplink';

import logoffConfirm from './logoffConfirm';

export default {
    data() {
        return {
            tips: [],
            isRead: false,
            hint: {
                title: '重要提示',
                content: '注销账号是不可撤销操作，注销后该账号将无法再次使用。',
            },
            showConfirm: false,
        };
    },
    components: {
        NavBar,
        Button,
        logoffConfirm,
    },
    methods: {
        cancel() {
            this.showConfirm = false;
            this.isRead = false;
        },
        logoffAction() {
            doLogoff().then(() => {
                destroyAccount(); // 账号注销
                goIndexTab('newsTab'); // 去首页
            });
        },
        getTips() {
            return getConfigByKey({
                key: 'fe-config_logoff',
            }).then((res) => {
                this.tips = res.data;
                this.$root.hidePlaceholder();
            });
        },
        logoff() {
            if (this.isRead) {
                logoffHint().then((res) => {
                    if (res.coinAmount) {
                        this.hint.content = `您账号内还有<span class="color-red">${res.coinAmount}</span>金币未提现，注销后金币余额会清零，且无法撤销。`;
                    }
                    this.showConfirm = true;
                }).catch((err) => {
                    toast(err.msg);
                });
            } else {
                toast('请点击我已阅读并同意');
            }
        },
    },
    created() {
        this.getTips();
    },
};
</script>

