}

.check-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    background: rgba(245, 245, 245, 1);
    border-radius: 3px;
    margin-right: 5px;
}

.checked-icon {
    background-image: url(./image/checked.png);
    background-position: center;
    background-size: 16px 11px;
    background-repeat: no-repeat;
}

.logoff-check {
    display: none;
